*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: rgba(240, 248, 255, 0.685);
  font-family:"	Regular, Bold, Italic, Bold Italic";
}
div{
  width: 100%;
  height: 100vh;
  background: #8e8e8e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center
  ;
}
h1{
  color: white;
  background: transparent;
  text-shadow: 0 2px 2px black;
}
input{
  width: 50%;
  padding: 10px 20px;
  border:none;
  outline: none;
  margin: 20px 0;
  text-align: center;
}
button{
  line-height: 24px;
  padding: 0 20px;
  background: #9b59b6;
  color: whitesmoke;
  border:2px solid #ecf0f1;
  outline: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;

}
